import React, {Component} from 'react';
import {connect} from 'react-redux';
import * as UI from '@vkontakte/vkui';
import './CurrencyRateDashboard.css';
import * as currencyRatesSelectors from '../store/currency_rates/reducer';

class CurrencyRateDashboard extends Component {

    constructor(props) {
        super(props);

        this.renderRow = this.renderRow.bind(this);
    }

    getPrettyDate(stringDate) {
        let date = new Date(Date.parse(stringDate));
        let day = date.getDate();
        let month = date.getMonth();
        let year = date.getFullYear();

        return ('0' + day).slice(-2) + '.' + ('0' + (month + 1)).slice(-2) + '.' + year.toString().substr(-2);
    }

    render() {
        if (!this.props.rates) {
            return (<UI.Div/>);
        }

        let currentDate = this.getPrettyDate(this.props.rates.Date);
        let previousDate = this.getPrettyDate(this.props.rates.PreviousDate);

        return (
          <UI.Div>
          Вбиваю "изготовить визитки" в Яндекс, вижу восемь рекламных объявлений. Это, кстати, не упрек поисковику, в идеальном мире их бы ещё больше было, по такому запросу только Директ и ожидается, человек купить услугу хочет, а не лекции слушать. Ровно половина рекламодателей встроили цену в заголовок или сниппет, они просят 850, 850, 865 и 2000 рублей за тысячу визиток. Никакой другой информации для выбора нет, все сайты одинаково обещают качественный сервис и безупречную печать. <br/> <br/>Я только для примера запрос вводил, реальный потребитель кликнет во все три с адекватной ценой и ещё парочку без цены, в часть позвонит и вынесет мозг менеджеру, и ещё не факт, что хоть где-то все-таки закажет. В итоге больше всех заработает Яндекс, по нему 5 платных кликов сделали, а исполнитель еле-еле сведет юнит-экономику, повышать цены нельзя, понижать расходы на привлечение нельзя, идеальная конкуренция. <br/> <br/>Японский #стартапдня Raksul показал, что и на таком трудном рынке срабатывают венчурные механизмы. Стартап называет себя маркетплейсом полиграфических услуг, на самом деле – просто большая полиграфическая компания. Заказчик выбирает на сайте, что он хочет, Raksul обеспечивает исполнение. Да, печатают в конечном итоге подрядчики, но какая по большому счету разница. Цену определяет стартап, уровень сервиса определяет стартап, ответственность за качество несет стартап, даже доставку организует стартап. Клиент не знает и не хочет знать конечного исполнителя. <br/> <br/>Как и любая жертва идеальной конкуренции, Raksul не должен был зарабатывать, но он и не хотел этого делать. Компания привлекала инвестиции, тратила их на агрессивную рекламу, низкие цены и высокое качество, росла и создавала собственный бренд. Через какое-то время средний потребитель запомнил, что хорошая печать сувенирки это Raksul, и с этого момента успех стал неизбежен. В изначальной формуле "всю прибыль забирает поисковик" поисковик исчез, а прибыль осталась. <br/> <br/>Запустился стартап в 2009 году, потратил 36 миллионов долларов инвестиций, в мае вышел на IPO, сейчас стоит 600 миллионов. В этом году японский бизнес пришел к рентабельности, но стартап обещает завоевать с ноля Индонезию, так что суммарно, наверное, по-прежнему будет минус.
          <br/><br/><a href="https://raksul.com/">https://raksul.com/</a>
           </UI.Div>


        );
    }

    renderRow(symbol, currency) {
        let isMore = currency.Value > currency.Previous;
        let color = isMore ? UI.colors.green : UI.colors.red;
        let arrow = isMore ? '↑' : '↓';
        return (
            <UI.ListItem before={<div className="currency_rate_dashboard__item_before">{symbol}</div>}>
                <div className="currency_rate_dashboard__item">
                    <div className="currency_rate_dashboard__item__column" style={{color: color}}>
                        {currency.Value} ₽ {arrow}
                    </div>
                    <div className="currency_rate_dashboard__item__column">
                        {currency.Previous} ₽
                    </div>
                </div>
            </UI.ListItem>
        );
    }
}

function mapStateToProps(state) {
    return {
        rates: currencyRatesSelectors.getCbrDailyCurrencyRates(state),
        cbrCurrencies: currencyRatesSelectors.getCbrCurrencyRateByCode(state),
    };
}

export default connect(mapStateToProps)(CurrencyRateDashboard);



